@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    transition: all .2s linear;
    outline: none;
}
html{
    font-size: 62.5%;
    scroll-behavior: smooth;
}
body{
    // overflow-x: hidden;
    width: 100%;
    height: 100%;
    font-family: 'Roboto', sans-serif;
    // background-color: #000;
}

// color: #0fBBe0;
// dark: #0d2154;
span{
    color: #0fBBe0;
}
ul{
    list-style-type: none;
}
a{
    text-decoration: none;
    color: #fff;
}
p{
    font-size: 1.4rem;
    line-height: 1.4rem;
}
h1{
    font-size: 3.5rem;
    @media screen and (max-width: 968px) {
        font-size: 3rem;
    }
}
h2{
    font-size: 3rem;
    @media screen and (max-width: 968px) {
        font-size: 2.5rem;
    }
}
.container{
    max-width: 1240px;
    margin: auto;
}
button{
    font-family: "Roboto", sans-serif;
    padding: .7rem 1.5rem;
    margin: 1rem 0;
    background-color: #0fBBe0;
    border: .1rem solid #0fBBe0;
    color: #f8f8f8;
    font-size: 1rem;
    font-weight: 500;
    display: flex;
    border-radius: .4rem;
    &:hover{
        background-color: #47dcff;
        border: .1rem solid #47dcff;
    }
}



// HEROO.................................................................................



.hero{
    width: 100%;
    height: 100vh;
    background: url(./component/images/cyber-bg.png)no-repeat center center/cover;
    .container{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .content{
        height: 95vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 1rem;
        margin-left: 6rem;
        h1{
            color: #f8f8f8;
            font-size: 6rem;
        }
        @media screen and (max-width: 968px) {
            margin-left: 1rem;
        }
        button{
            font-size: 2rem;
        }
    }
}


// NAVBAR..........................................................

.navbar{
    width: 100%;
    height: 9vh;
    position: absolute;
    // z-index: 10000;
.container{
    align-items: center;
    justify-content: space-between;
    display: flex;
    .logo{
        z-index: 10000;
        a{
            font-size: 4rem;
            font-weight: 600;
            color: #f8f8f8;
            .fa-layer-group{
                padding: 1rem;
                color: #0fBBe0;
            }
        }
    }
    .nav-menu{
        display: flex;
        padding-top: 1rem;
        z-index: 1;
        .nav-items{
            padding: 2rem;
            a{
                font-size: 2rem;
                color: #f8f8f8;
            }
        }
        .nav-items-mbl{
            button{
                font-size: 2rem;
                margin-left: 1rem;
                a{
                    color: #f8f8f8;
                }
            }
        }
        @media screen and (max-width: 968px) {
            flex-direction: column;
            position: absolute;
            background-color: rgba(0,0,0,.8);
            top: 0;
            height: 100vh;
            width: 100%;
            left: -100%;
            justify-content: center;
            z-index: 10;
            .nav-items{
                text-align: center;
                font-weight: 600;
            }
            .nav-items-mbl{
                margin: 0 auto;
                margin-top: 2rem;
                button{
                    padding: .7rem 2rem;
                }
            }
        }
    }
    .nav-menu.active{
        left: 0;
        transition: .5s ease-in;
        // z-index: -1;
        a{
            font-size: 2rem;
        }
    }
    .icons{
        display: none;
        @media screen and (max-width: 968px) {
            display: block;
            color: #fff;
            font-size: 3rem;
            padding-right: 2rem;
            z-index: 10000;
        }
    }
    @media screen and (max-width: 1260px) {
        padding: 0 4rem;
    }
    @media screen and (max-width: 730px) {
        padding: 0 1rem;
    }
    @media screen and (max-width: 500px) {
        padding: 0 0;
    }
}
}




// DATA....................................................


.data{
    width: 100%;
    height: 100vh;
    position: relative;
    background: rgba(0,0,0,.1);
    &:before{
        content: "";
        position: absolute;
        background: url(./component/images/data-bg.png)no-repeat center center/cover;
        height: 100%;
        width: 100%;
        z-index: -1;
    }
    .content{
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        left: 50%;
        top: 30%;
        width: 50%;
        @media screen and (max-width: 968px) {
            left: 0;
            width: 100%;
            padding: 1rem;
            background: rgba(255,255,255,.7);
        }
        @media screen and (max-width: 430px) {
            top: 15rem;
        }
        h2{
            font-size: 4rem;
        }
        p{
            font-size: 2rem;
            line-height: 2.5rem;
            padding-right: 3rem;
        }
        button{
            font-size: 2rem;
        }
    }
}


// CLOUDS.................................................................


.cloud{
    width: 100%;
    height: 100vh;
    position: relative;
    background: rgba(0,0,0,.3);
    &::before{
        content: "";
        position: absolute;
        background: url(./component/images/cloud-bg.png)no-repeat center center/cover;
        height: 100%;
        width: 100%;
        z-index: -1;
    }
    .container{
        position: relative;
        height: 100%;
        .content{
            position: absolute;
            left: 0;
            top: 30%;
            width: 50%;
            padding: 1rem;
            h2{
                color: #f8f8f8;
                font-size: 4rem;
            }
            p{
                color: #f8f8f8;
                font-size: 1.7rem;
                line-height: 2.5rem;
                margin: 1rem 0;
            }
            button{
                font-size: 1.7rem;
            }
            @media screen and (max-width: 968px) {
                left: 0;
                width: 100%;
            }
        }
    }
}

// FOOTER................................................................


.footer{
    width: 100%;
    height: 60vh;
    position: relative;
    background: rgba(255,255,255,.7);
    // background: rgba(0,0,0,.8);
    &::before{
        content: "";
        position: absolute;
        background: url(./component/images/footer-bg.jpg)no-repeat center center/cover;
        height: 100%;
        width: 100%;
        z-index: -1;
    }
    .container{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding-top: 2rem;
        .top{
            width: 100%;
            padding: 1rem;
            display: flex;
            justify-content: space-between;
            .logo-footer{
                display: flex;
                align-items: center;
                i{
                    font-size: 3rem;
                margin-right: 1rem;
                color: #0fBBe0;
                cursor: pointer;
                }
            }
            .fa-arrow-up{
                font-size: 2rem;
            }
        }
    }
    .col-container{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
        padding: 1rem;
        width: 100%;
        h3{
            margin: 1rem 0 2rem 0;
            text-transform: uppercase;
        }
        p{
            margin: .5rem 0;
            font-size: 1.7rem;
            padding: 1rem 0;
        }
        form{
            width: 100%;
            margin-bottom: .6rem;
            position: relative;
            input{
                padding: 1rem;
                width: 100%;
                outline: none;
                font-size: 1.7rem;
                border: .1rem solid #aaa;
                border-radius: .4rem;
                &:focus{
                    color: #0fBBe0;
                    outline: #0fBBe0;
                    box-shadow: 0 0 .8rem 0 #0fBBe0;
                }
            }
            .fa-envelope{
                position: absolute;
                font-size: 2rem;
                margin: 1.5rem;
                right: .2rem;
                color: #aaa;
            }
            .social-group{
                padding-top: 2rem;
                i{
                margin: 1rem 1rem 1rem 0;
                padding: .5rem;
                background-color: #1c343a;
                border-radius: .4rem;
                color: #f8f8f8;
                font-size: 3rem;
                }
            }
            @media screen and (max-width: 968px) {
                grid-column: 1/span 2;
            }
        }
        @media screen and (max-width: 968px) {
            grid-template-columns: 2fr 2fr;
        }
    }
    @media screen and (max-width: 968px) {
        height: 100%;
    }
}


// RECOVERY..................................................


.recovery{
    width: 100%;
    height: 100vh;
    background: rgba(0,0,0,.4);
    position: relative;
    &::before{
        content: "";
        position: absolute;
        background: url(./component/images/recovery-bg.png)no-repeat center center/cover;
        height: 100%;
        width: 100%;
        z-index: -1;
    }
    .container{
        position: relative;
        height: 100%;
        .content{
            position: absolute;
            top: 30%;
            width: 50%;
            left: 0;
            padding: 1rem;
            h2{
                font-size: 4rem;
                color: #f8f8f8;
            }
            p{
                font-size: 1.7rem;
                line-height: 2.5rem;
                color: #f8f8f8;
                margin: 1rem 0;
            }
            button{
                font-size: 1.7rem;
            }
            @media screen and (max-width: 968px) {
                width: 100%;
            }
        }
    }
}


