// CONTACT....................................................



.contact{
    width: 100%;
    height: 100vh;
    position: relative;
    background: rgba(0,0,0,.5);
    &::before{
        content: "";
        position: absolute;
        background: url(./component/images/contact-bg.png)no-repeat center center/cover;
        width: 100%;
        height: 100%;
        z-index: -1;
    }
    .container{
        height: 100%;
        .form-container{
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100%;
            div{
                display: flex;
                flex-direction: column;
            }
            form{
                width: 50%;
                margin: 1rem;
                h1{
                    color: #f8f8f8;
                }
                label{
                    margin: 2rem 0 .7rem 0;
                    display: none;
                }
                input,textarea{
                    padding: .5rem;
                    font-size: 1.4rem;
                    color: #f8f8f8;
                    margin: 1rem 0;
                    font-family: "Roboto", sans-serif;
                    background: rgba(0,0,0,.15);
                    box-shadow: 0 .8rem 3.2rem 0 rgba(31,38,135,0.37);
                    // backdrop-filter: blur(.4rem);
                    border-radius: .4rem;
                    border: .1rem solid rgba(0,0,0,.15);
                    &:focus{
                        outline: #0fBBe0;
                        box-shadow: 0 0 1.2rem 0 #0fBBe0;
                    }
                }
                @media screen and (max-width: 968px) {
                    width: 100%;
                }
                button{
                    font-size: 1.6rem;
                }
            }
        }
    }
}